.services-heading{
    padding-bottom:5%;
    font-size: 48px;

}
.column {
    float: left;
    width: 33.33%;
    padding: 10px;
  }
  
  /* Clear floats after the columns */
  .row:after {
    content: "";
    display: table;
    clear: both;
  }

  
  /* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
  @media screen and (max-width: 600px) {
    .column {
      width: 100%;
    }
  }
  .services-container{
      
      padding-top: 5%;
      padding-left: 18%;
      padding-right: 18%;
      background-color: #fcf7ed;
  }
  .service-row-1{
      padding-bottom: 10%;
  }
  .img_1{
      border: 1px solid;
  }
  .img_2{
    width: 92%;
    border: 1px solid;
}
  .img_3{
    width: 94%; 
    border: 1px solid;
}
.service_heading{
    padding-top: 15%;
    text-align: center;
}
.service_description{
    text-align: center;
    padding-top:5%;
}
